  <div class="LG_container">
    <header class="c_header">
      <nav>
        <ul>
          <li @click="module_choose(1)">ÁîµÂΩ±</li>
          <li @click="module_choose(2)">È¢úËâ≤Ë°®</li>
          <li @click="module_choose(3)">Èü≥‰πê</li>
          <li @click="module_choose(4)">‰∏™‰∫∫ËÆ°Âàí</li>
          <li @click="module_choose(5)">ÂäüËÉΩÊ®°Âùó</li>
          <li @click="module_choose(6)">È°πÁõÆ</li>
        </ul>
        <ul>
          <li id="lg_li" v-show="showR">ÁôªÂΩï</li>
          <li v-show="showR">Ê≥®ÂÜå</li>
          <li v-show="!showR" list="setting">
            <img :src="author_icon" alt="ÁôªÂΩïÂ§¥ÂÉè" class="author_icon" />
            <select class="setting_value">
              <option value="ËÆæÁΩÆ">ËÆæÁΩÆ</option>
              <option value="ÈÄÄÂá∫ÁôªÂΩï">ÈÄÄÂá∫ÁôªÂΩï</option>
              <option value="ÈÄÄÂá∫ÁôªÂΩï">ÂàáÊç¢Áî®Êà∑</option>
            </select>
          </li>
        </ul>
      </nav>
    </header>
    <main class="c_main">
      <aside class="aside_l aside_2">
        <ul class="aside_text img_con"></ul>
      </aside>
      <div class="mainDiv">
        <!-- ÈªòËÆ§ÊòæÁ§∫ÂÜÖÂÆπ -->
        <div class="ulList" v-show="display">
          <ul class="ulList_ul">
            <li class="ulList_li titles" id="li_style">{{ title }}</li>
            <li class="ulList_li titles" id="li_style">
              Âú®‰∏ÄËµ∑ÁöÑÊó∂Èó¥:{{ togethers }}
            </li>
            <li class="ulList_li titles" id="li_style">
              ÊèêÁ§∫:{{ memorial_day }}
            </li>
            <li class="ulList_li titles" id="li_style">{{ times }}</li>
            <li class="ulList_li titles" id="li_style">{{ weathers }}</li>
          </ul>
        </div>
        <!-- ‰∏ªË¶ÅÁöÑÂäüËÉΩÁïåÈù¢ -->
        <div class="moduless" v-show="!display">
          <div>
            <img
              src="../assets/icon/delete.svg"
              alt="ÂÖ≥Èó≠"
              @click="module_choose(0)"
            />
          </div>
          <div>
            <router-view></router-view>
          </div>
        </div>
      </div>
      <aside class="aside_r">Âè≥‰æßÊÇ¨ÊµÆÊù°</aside>
    </main>
    <footer class="c_footer">
      <ul v-show="!display">
        <li class="titles">Âú®‰∏ÄËµ∑ÁöÑÊó∂Èó¥:{{ togethers }}</li>
        <li class="titles">ÊèêÁ§∫:{{ memorial_day }}</li>
        <li class="titles">{{ times }}</li>
        <li class="titles">{{ weathers }}</li>
      </ul>
